@tailwind base;
@tailwind components;
@tailwind utilities;

/* Additional Custom Styles */
.toggle-checkbox {
  width: 1.25rem;
  height: 1.25rem;
  background: #374151;
  border-radius: 50%;
  display: inline-block;
  transition: background 0.2s ease-in-out;
}

.toggle-checkbox:checked {
  background: #2563eb;
}
